.afterServiceContainer img, .beforeServiceContainer img{
    width: 300px;
    height: 250px;
    margin: 20px;
    box-shadow: 0px 3px 10px -6px;
}

.afterServiceContainer, .beforeServiceContainer {
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
}

