.home{
  padding: 20px 0px;
}

#homeImg{
    width: 100%;
    box-shadow: 0px 3px 10px -6px;
}

.mainQoute {
  text-align: center;
}

.mainQoute h2 {
  font-size: 49px;
  margin: 1em auto 0 auto;
  font-weight: 600;
  content: open-quote;
}

.mainQoute p {
  font-size: 28px;
  margin: auto;
  line-height: 45px;
}

blockquote h2::before {
  content: open-quote;
  /* text-align: center; */
  display: inline;
  font-size: 1.5em;
  line-height: 1em;
  vertical-align: bottom;
  height: 0.5em;
  color: rgba(36, 36, 36, 0.4);
}

p::after {
  content: close-quote;
  color: rgba(36, 36, 36, 0.4);
}

